<template>
  <!--首页-->
  <div class="home">
    <!--顶部-->
    <div class="header">
      <mt-header fixed title="网站名称">
        <router-link to="/scan" slot="left">
          <mt-button class="icon-thumb_up"></mt-button>
        </router-link>

        <router-link to="/message" slot="right">
          <mt-button class="icon-thumb_up"></mt-button>
        </router-link>
      </mt-header>
    </div>

    <!--搜索框-->
    <div class="search">
      <mt-search v-model="searchValue" cancel-text="取消" placeholder="搜索">
        <mt-cell v-for="(item, index) in filterResult"
                 :key="index"
                 :title="item.title"
                 :value="item.value"
                 @click.native="$router.push({ path: '/goods', query: { search: item.title } })">
        </mt-cell>
      </mt-search>
    </div>

    <!--轮播图-->
    <div class="swiper">
      <mt-swipe :auto="3000" @change="handleChange">
        <mt-swipe-item v-for="(item, index) in bannerList" :key="index">
          <router-link to="/">
            <img :src="'/static/images/banners/' + item.picUrl" :alt="item.alt" />
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>

    <!--推荐商品-->
    <div class="best">
      <div class="title">
        <i class="icon-thumb_up"></i> <span class="text">推荐商品</span>
      </div>

      <div class="main">
        <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
          <li v-for="(item, index) in bestList" :key="index">
            <router-link to="/">
              <div class="image">
                <img  class="img" v-lazy="'/static/images/goods/' + item.productImage" alt="" />
              </div>
              <div class="name"><p>{{item.productName}}</p></div>
              <div class="introduce"><p>{{item.productId}}</p></div>
              <div class="price"><p>￥{{item.salePrice}}</p></div>
            </router-link>
          </li>
        </ul>

        <p v-show="!noMore" class="loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          加载中...
        </p>

        <p v-show="noMore" class="no-more">没有更多啦</p>
      </div>
    </div>

    <!--页卡栏-->
    <tabbar/>
  </div>
</template>

<script>
  import { MessageBox } from 'mint-ui'
  import axios from 'axios'
  import Tabbar from '@/components/Tabbar'

  export default {
    data () {
      return {
        searchValue: '',
        searchResult: [
          { title: 'Apple', value: 'apple' },
          { title: 'Banana', value: 'banana' },
          { title: 'Orange', value: 'orange' },
          { title: 'Durian', value: 'durian' },
          { title: 'Lemon', value: 'lemon' },
          { title: 'Peach', value: 'peach' },
          { title: 'Cherry', value: 'cherry' },
          { title: 'Berry', value: 'berry' },
          { title: 'Core', value: 'core' },
          { title: 'Fig', value: 'fig' },
          { title: 'Haw', value: 'haw' },
          { title: 'Melon', value: 'melon' },
          { title: 'Plum', value: 'plum' },
          { title: 'Pear', value: 'pear' },
          { title: 'Peanut', value: 'peanut' },
          { title: 'Other', value: 'other' }
        ],
        bannerList: [],
        bestList: [],
        loading: false,
        page: 0,
        pageSize: 6,
        noMore: false
      }
    },
    computed: {
      filterResult() {
        return this.searchResult.filter((value, index) =>
          new RegExp(this.searchValue.title, 'i').test(value) && index < 10);
      }
    },
    created () {
      let self = this;

      axios.get('/mock/5a2967fc70188c51fc72ed39/shop/banner/list').then(res=>{
        if (res.status === 200) {
          self.bannerList = res.data.data;
        } else {
          MessageBox({ title: '警告', message: res.statusText });
        }
      });
    },
    mounted() {
      this.getGoodsList();
    },
    methods: {
      handleChange(index) {
//        console.log(index);
      },
      loadMore() {
        let self = this;
        this.loading = true;

        setTimeout(() => self.getGoodsList(), 500);
      },
      getGoodsList() {
        this.page++;
        let self = this;

        axios.get('/mock/5a2967fc70188c51fc72ed39/shop/goods/list').then(res=>{
          if (res.status === 200) {
            let newList = [];

            if (self.page*self.pageSize <= res.data.data.length){
              for(let i=(self.page-1)*self.pageSize; i<self.page*self.pageSize; i++){
                newList.push(res.data.data[i]);
              }
              self.loading = false;
            } else {
              for(let i=(self.page-1)*self.pageSize; i<res.data.data.length; i++){
                newList.push(res.data.data[i]);
              }
              self.noMore = true;
            }

            self.bestList = self.bestList.concat(newList);
          } else {
            MessageBox({ title: '警告', message: res.statusText });
          }
        })
      }
    },
    components: {
      Tabbar
    }
  }
</script>

<style lang="scss" scoped>
  @import '~@/assets/scss/const.scss';
  @import '~@/assets/scss/mixin.scss';

  $swiperHeight: 150px;

  .home {
    height: 100%;
    height: 100vh;
    .header {
      .mint-header {
        z-index: 9;
      }
    }

    .search {
      @include box-position;
      z-index: 7;
      .mint-search {
        height: auto;
      }
    }

    .swiper {
      @include box-position;
      height: $swiperHeight;
      img {
        width: 100%;
        height: $swiperHeight;
      }
    }

    .best {
      @include box-position;
      .title {
        @include child-center;
        width: 100%;
        margin: 1rem 0;
        color: $color-theme;
        font-weight: 600;
        letter-spacing: .5rem;
      }

      .main {
        ul {
          width: 100%;
          display: flex;
          flex-wrap: wrap;
          li {
            @include onepx-x('right', $color-bgc);
            width: 50%;
            height: 270px;
            background-color: $color-box-bgc;
            border-bottom: 2px solid $color-bgc;
            padding-top: 1rem;
            a {
              &>div {
                width: 100%;
                @include child-center;
              }

              .image {
                @include square-img;
                .img[lazy=loading] {
                  width: 40px;
                  left: 50%;
                  margin-left: -20px;
                }
              }

              .name {
                padding-top: .5rem;
                p {
                  @include ellipsis;
                  font-size: $font-14px;
                  font-weight: bold;
                  color: $color-md-font;
                }
              }

              .introduce {
                padding-top: .25rem;
                p {
                  @include ellipsis;
                  font-size: $font-12px;
                  color: $color-l-font;
                }
              }

              .price {
                padding-top: .5rem;
                p {
                  font-size: $font-16px;
                  color: $color-price;
                }
              }
            }
          }
        }

        .loading, .no-more {
          text-align: center;
          height: 100px;
          line-height: 50px;
          color: $color-n-font;
        }
      }
    }


  }

  @media screen and (min-width: 992px) {
    .home {
      .search, .swiper, .best {
        width: 80%;
        margin: 0 auto;
      }

      .swiper {
        height: 250px;
        img {
          height: 250px;
        }
      }

      .best {
        .main {
          ul {
            li {
              width: 25%;
              height: 350px;
            }
          }
        }
      }
    }
  }

</style>



